<template>
  <div class="login-bg">
    <div class="topbar">
      <div class="topbar-login-title">
        <img src="~@/assets/images/login_logo.png" />
      </div>

      <div class="topbar-back">
        <!-- http://119.84.149.34:20011/Sociology/#/ -->
        <span @click="handleSearchInfo">
          <svg-icon icon-class="sousuofangdajing"></svg-icon>
          <span>信息查询</span>
        </span>

        <span @click="handleBackSite">
          <img src="~@/assets/images/back-home.png" />
          <span>返回首页</span>
        </span>
      </div>
    </div>
    <el-card class="login-form-layout">
      <el-form
        auto-complete="on"
        :model="loginForm"
        :rules="loginRules"
        ref="loginForm"
        label-position="left"
      >
        <!-- <div style="text-align: center">
          <svg-icon
            icon-class="login-mall"
            style="width: 56px; height: 56px; color: #409eff"
          ></svg-icon>
        </div> -->
        <!-- <h2 class="login-title color-main">mall-admin-web</h2> -->
        <el-form-item>
          <div class="login-header">
            <div style="font-weight: bold; font-size: 24px">登录</div>
            <div class="register" @click="$router.push('/register')">
              <span>没有账号?</span>
              <span>立即注册</span>
            </div>
          </div>
        </el-form-item>
        <el-form-item prop="email">
          <el-input
            name="email"
            type="text"
            v-model="loginForm.email"
            auto-complet="on"
            placeholder="请输入邮箱"
          >
            <span slot="prefix">
              <svg-icon icon-class="youxiang" class="color-main"></svg-icon>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            name="password"
            :type="pwdType"
            @keyup.enter.native="handleLogin"
            v-model="loginForm.password"
            placeholder="请输入密码"
          >
            <span slot="prefix">
              <svg-icon icon-class="mima" class="color-main"></svg-icon>
            </span>

            <span slot="suffix" @click="showPwd" v-if="pwdType">
              <svg-icon icon-class="mimabiyan" class="color-main"></svg-icon>
            </span>
            <span slot="suffix" @click="showPwd" v-else>
              <svg-icon icon-class="yanjing-" class="color-main"></svg-icon>
            </span>
          </el-input>
        </el-form-item>

        <el-form-item v-if="dragVerifShow">
          <DragVerify ref="drags"></DragVerify>
        </el-form-item>

        <el-form-item style="text-align: center">
          <el-button
            style="width: 45%"
            type="primary"
            :loading="loading"
            @click.native.prevent="handleLogin"
            >登录</el-button
          >
        </el-form-item>
        <div style="text-align: center; color: #409eff; font-size: 14px">
          为了良好的用户体验，请使用谷歌浏览器
        </div>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import DragVerify from '@/components/common/DragVerify.vue'
import user from '@/api/user.js'
import { setToken } from '@/utils/auth'
// import { isvalidUsername } from "@/utils/validate";
// import { setSupport, getSupport, setCookie, getCookie } from "@/utils/support";

export default {
  name: 'login',
  components: {
    DragVerify
  },
  data () {
    const validateEmail = (rule, value, callback) => {
      const emailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!emailReg.test(value)) {
        callback(new Error('请输入正确格式的邮箱地址'))
      } else {
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (value.length < 4) {
        callback(new Error('密码不能小于4位'))
      } else {
        callback()
      }
    }
    return {
      dragVerifShow: 1,
      loginForm: {
        email: '',
        password: ''
      },
      loginRules: {
        email: [{ required: true, trigger: 'blur', validator: validateEmail }],
        password: [
          { required: true, trigger: 'blur', validator: validatePass }
        ]
      },
      loading: false,
      pwdType: 'password'
    }
  },
  created () {
    const date = new Date()
    console.dir(date)
  },
  methods: {
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    async handleLogin () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // let isSupport = getSupport();
          // if(isSupport===undefined||isSupport==null){
          //   this.dialogVisible =true;
          //   return;
          // }
          if (!this.$refs.drags.$refs.Verify.isPassing) {
            this.$message.error('请滑动滑块以验证')
            return false
          }
          this.loading = true
          user
            .login(this.loginForm)
            .then((res) => {
              console.log(res)
              if (res) {
                if (!res.state) {
                  this.$message.warning('账户已被禁用，请联系管理员')
                  // return false;
                } else {
                  this.$message.success('登录成功')
                  setToken(res.user_name)
                  localStorage.setItem('user_name', res.user_name)
                  localStorage.setItem('user_role', res.role.substr(0, 1))
                  this.$router.replace({ path: '/' })
                }
              } else {
                this.$message.error('账号或密码错误')
                this.dragVerifShow = 0
                setTimeout(() => {
                  this.dragVerifShow = 1
                })
              }
              this.loading = false
              // if (validResult) {
              //   setToken(this.loginForm.user_name);
              //   localStorage.setItem("name", this.loginForm.user_name);
              //   this.$message.success("登录成功");
              //   this.$router.replace({ path: "/" });
              // } else {
              //   this.$message.error("账号或密码错误");
              //   this.dragVerifShow = 0;
              //   setTimeout(() => {
              //     this.dragVerifShow = 1;
              //   });
              // }
              // this.loading = false;
            })
            .catch((err) => {
              console.log(err)
              this.loading = false
            })

          // this.$store.dispatch('Login', this.loginForm).then(() => {
          //   this.loading = false;
          //    // setCookie("user_name", this.loginForm.user_name, 15);
          //   // setCookie("password", this.loginForm.password, 15);
          //   this.$router.push({ path: '/' })
          // }).catch(() => {
          //   this.loading = false
          // })

          return false
        }
      })
    },
    handleBackSite () {
      // window.location.href = "http://119.84.149.34:20011/Sociology/#/";
      this.$router.push({ path: '/' })
    },
    handleSearchInfo () {
      this.$router.push({ path: '/info-search' })
    }
  }
}
</script>

